* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  background: white;
  font-size: 1.1rem;
  font-family: "Quicksand", sans-serif;
  height: 100%;
}

.NewRowForm {
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap;
}

.NewRowForm label, 
.NewRowForm p {
  min-width: 100%;
  margin-bottom: 0.2rem;
  font-size: 1rem;
}

.NewRowForm input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  margin-bottom: 0.5rem;
  padding: 0 1rem;
  font-size: 1rem;
  outline: none;
  width: 100%;
}

.NewRowForm button {
  padding: 0 1.3rem;
  border: none;
  background: #479761;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin: 0 auto;
  cursor: pointer;
  -webkit-transition: background 0.4s ease-out;
  transition: background 0.4s ease-out;
  outline: none;
}

.NewRowForm button:hover {
  background: #479761;
  opacity: 0.75;
}

.NewRowForm input,
.NewRowForm button {
  font-family: "Quicksand", sans-serif;
  height: 2rem;
}

.Row {
  display: grid;
  grid-template-columns: auto auto;
}

.Row-row {
  position: relative;
  -webkit-transition: opacity 0.4s linear;
  transition: opacity 0.4s linear;
  margin-bottom: 1rem;
}

.Row-row span{
  color: white;
}

.Row-buttons {
  flex-shrink: 0;
  padding-left: 0.7em;
  margin-left: auto;
}

.Row-buttons button {
  border: none;
  font-size: 1em;
  margin: 0.4em;
  background: none;
  -webkit-appearance: none;
  cursor: pointer;
  color: #fff;
}

.Row-edit-form {
  display: flex;
  flex-wrap: wrap;
}

.Row-edit-form label {
  min-width: 100%;
  margin-bottom: 0.2rem;
  font-size: 1rem;
}

.Row-edit-form input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  margin-bottom: 0.5rem;
  padding: 0 1rem;
  font-size: 1rem;
  outline: none;
  width: 100%;
}

.Row-edit-form button {
  padding: 0 1.3rem;
  border: none;
  background: #479761;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin: 0 auto 2rem auto;
  cursor: pointer;
  -webkit-transition: background 0.2s ease-out;
  transition: background 0.2s ease-out;
}

.Row-edit-form button:hover {
  background: #479761;
  opacity: 0.75;
}

.Row-edit-form input,
.Row-edit-form button {
  font-family: "Quicksand", sans-serif;
  height: 2rem;
}

.Dictionary {
  margin: 4rem auto;
  padding: 2rem 3rem 3rem;
  max-width: 600px;
  background: #19181a;
  color: #479761;
  box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, 0.1);
  overflow: hidden;
}

.Dictionary ul {
  margin-top: 2.6rem;
  list-style: none;
}

.Dictionary h1 {
  font-weight: normal;
  font-size: 2.6rem;
  letter-spacing: 0.05em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

